body {
   background-image: url("../slider/bg.jpg");
   background-position: center;
   background-size: cover;
   min-height: 100vh;
   // overflow: hidden;
}

html {
   font-size: 24px;
}

.contSlider32 {
   position: relative;
   display: grid;
   grid-template-rows: 15px 1fr;
}

.contSlider32 picture {
   width: 95%;
   height: 90px;
   transform: translateY(20px);
   max-width: 350px;
   z-index: 3;
   margin: 0 auto;
}

.contSlider32 picture img {
   display: block;
   width: 100%;
   height: 100%;
   object-fit: contain
}

.swiper {
   user-select: none;
   height: 100vh;
   // background: red;
   // background: green;
}

.swiper .swiper-slide {
   // width: 50%;
   text-align: center;
   font-size: 18px;
   background: #fff;
   display: flex;
   justify-content: center;
   align-items: center;
   max-width: 550px;
   width: 95%;
   height: 100%;
   background: rgba(0, 0, 0,0);
   // background: rgba(0, 0, 0,1)
}

.swiper .swiper-slide>section {
   width: 100%;
   height: 500px;
   // background: blue;
   // background: rgba(0, 0, 0, .407);
   // backdrop-filter: blur(15px);
   position: relative;
}

.swiper .swiper-slide>section picture {
   // background: red;
   display: block;
   width: 100%;
   height: 500px;
   height: auto;
   background: purple;
   padding: 10px 0;
}

.swiper .swiper-slide>section picture::after {
   content: '';
   display: block;
   width: 200px;
   height: 100px;
   position: absolute;
   top: -60px;
   left: -70px;
   // background: red;
   background-image: url('./src/logo_elPortalR.png');
   background-size: contain;
   background-position: center;
   background-attachment: fixed;
   background-repeat: no-repeat;
   transform: rotate(-.085turn);
}
.swiper .swiper-pagination-bullet{
   width: 15px;
   height: 15px;
   background: rgba(2, 105, 0, 0.374);
   opacity: 1;
}
.swiper .swiper-pagination-bullet-active{
   width: 15px;
   height: 15px;
   background: rgb(3, 167, 0);
}
.swiper .swiper-slide>section picture img {
   // background: green;
   display: block;
   width: 100%;
   height: 100%;
   transform: scaleY(115%);
   object-fit: contain;
}

